import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';

import { Person } from '../person';

@Component({
  selector: 'app-person-form',
  templateUrl: './person-form.component.html',
  styleUrls: ['./person-form.component.css']
})
export class PersonFormComponent implements OnInit {
  @Output('search') eSearch: EventEmitter<any> = new EventEmitter<any>();
  
  @Input('form') form: FormGroup = this.getFormGroup();
  @Input('value') person: Person = new Person();

  constructor(private fb: FormBuilder) { }

  ngOnInit() {
    this.patchValue(this.person);
  }

  onSearch() {
    this.eSearch.emit(this.person);
  }

  patchValue(person: Person) {
    this.form.patchValue(person);
  }

  getValue() {
    return this.form.value;
  }

  disable() {
    this.form.disable();
  }

  getFormGroup() {
    return this.fb.group({
      _id: this.fb.control(''),
      first_name: this.fb.control('', Validators.required),
      second_name: this.fb.control(''),
      last_name: this.fb.control('', Validators.required),
      gender: this.fb.control(''),
      birthdate: this.fb.control(''),
      phone: this.fb.control('', Validators.required),
      email: this.fb.control(''),
      picture: this.fb.control('')
    });
  }

}



