import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { SpecialtiesService } from '../../+specialties/specialties.service';
import { FormDataService } from '../../shared/form-data.service';

import { Doctor } from '../doctor';

@Component({
  selector: 'app-doctors-form',
  templateUrl: './doctors-form.component.html',
  styleUrls: ['./doctors-form.component.css'],
  providers: [SpecialtiesService]
})
export class DoctorsFormComponent implements OnInit {
  @Input('formValues') params: Doctor;

  @Output('submited') 
    submited: EventEmitter<any> = new EventEmitter<any>();
  @Output('closed') 
    closed: EventEmitter<string> = new EventEmitter<string>();
  @Output('search') 
    eSearch: EventEmitter<string> = new EventEmitter<string>();
  @Output('imageselect')
    eImageSelect: EventEmitter<string> = new EventEmitter<string>();

  form: FormGroup;
  result: any;
  tags$: Observable<any>;
  achievements: any[];
  doctorates: any[];
  locations: any[];

  constructor(
    private fb: FormBuilder,
    private _specialtiesService: SpecialtiesService,
    private _formDataService: FormDataService
  ) {
    this.tags$ = this._specialtiesService.get();
  }

  ngOnInit() {
    this.getFormData();

    this.achievements = [
      {id: 'amb', display: '  '},
      {id: 'surg', display: ''},
      {id: 'ped', display: ''},
      {id: 'big', display: ' '}
    ];

    this.doctorates = [
      {id: 'default', display: ''},
      {id: 'top', display: '  '},
      {id: 'kmn', display: '  '}
    ];

    this.locations = [
      {id: 'bikovo', display: ''},
      {id: 'ostr', display: ''}
    ];

    this.form = this.fb.group({
      _id: this.fb.control(''),
      tags: this.fb.control(''),
      experience: this.fb.control('', Validators.required),
      comments: this.fb.control(''),
      certificates: this.fb.array([]),
      achievements: this.fb.group({
        amb: this.fb.control(''),
        surg: this.fb.control(''),
        ped: this.fb.control(''),
        big: this.fb.control('')
      }),
      doctorate: this.fb.control(''),
      locations: this.fb.control(''),
      active: this.fb.control(''),
      personId: this.fb.control(''),
      person: this.fb.group({
        //_id: this.fb.control(''),
        first_name: this.fb.control(''),
        second_name: this.fb.control(''),
        last_name: this.fb.control(''),
        gender: this.fb.control(''),
        birthdate: this.fb.control(''),
        phone: this.fb.control(''),
        email: this.fb.control(''),
        picture: this.fb.control('')
      })
    });

    this.patchValue(this.params);
  }

  patchValue(params: Doctor) {
    if(params.achievements !== undefined && Array.isArray(params.achievements)) {
      params.achievements = this.expandAchievs(params.achievements);
    }

    this.patchCertificatesControl(params.certificates);
    this.form.patchValue(params);
  }
  
  onSubmit() {
    this.result = this.form.valid ? this.form.value : false;
    
    this.result.achievements = this.collapseAchievs(this.result.achievements);
    //console.log(this.result);
    this.submited.emit(this.result);
  }

  onClose() {
    this.closed.emit();
  }

  onSearch() {
    this.eSearch.emit(this.form.controls['person'].value);
  }

  saveFormData() {
    this._formDataService.set(this.form.value);
  }

  getFormData() {
    if(this._formDataService.check()) {
      this.params.setParams(this._formDataService.get());
      this._formDataService.clear();
    }
  }

  setSelectedFiles(files) {
    this.eImageSelect.emit();
    this.getFormData();
    this.params.certificates = files;
    this.patchValue(this.params);
  }

  private expandAchievs(elements) : any {
    let achievements = {};
    elements.forEach(element => {
      achievements[element] = true;
    });
    return achievements;
  }

  private collapseAchievs(object: {}) : string[] {
    let achievements = [];
    for(let key in object) {
      if(object[key]) achievements.push(key);
    }
    return achievements;
  }

  private createCertControl(file) {
    let control = this.fb.group({
      _id: this.fb.control(''),
      title: this.fb.control('')
    });

    control.patchValue(file);
    return control;
  }

  private patchCertificatesControl(files) {
    let certControl = (<FormArray>this.form.controls['certificates']);
    this.resetArrayControl(certControl);

    files.forEach((file) => 
      certControl.push(this.createCertControl(file))
    );
  }

  private resetArrayControl(arrayControl: FormArray) {
    let controlsNumber = arrayControl.controls.length;
    for(let i = 0; i < controlsNumber; i++) {
      arrayControl.removeAt(0);
    }
  }

}



