import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterializeModule } from "angular2-materialize/src";
import { SharedModule } from '../shared/shared.module';

import { DoctorsListComponent } from './doctors-list/doctors-list.component';
import { DoctorsFormComponent } from './doctors-form/doctors-form.component';

import { DoctorsService } from './doctors.service';

@NgModule({
  imports: [
    CommonModule,
    ReactiveFormsModule, FormsModule,
    MaterializeModule,
    SharedModule
  ],
  providers: [DoctorsService],
  declarations: [DoctorsListComponent, DoctorsFormComponent],
  exports: [DoctorsListComponent, DoctorsFormComponent]
})
export class DoctorWidgetModule {  }


