import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { ToolsService } from '../../shared/tools.service';

import { PaymentsService } from '../payments.service';
import { Payment } from '../payment';

@Component({
  selector: 'app-payments',
  templateUrl: './payments.component.html',
  styleUrls: ['./payments.component.css']
})
export class PaymentsComponent implements OnInit, OnDestroy {
  payments$: Observable<Payment[]>;

  constructor(
    private _ps: PaymentsService,
    private _tools: ToolsService,
    private _router: Router
  ) { }

  ngOnInit() {
    this.reloadPaymnet();
    
    this._ps.getChangePaymentsEvents().subscribe((result: any) => {
      this.actionForSocketEvents(result);
      this.reloadPaymnet();
    });
  }

  reloadPaymnet(params?: any) {
    this.payments$ = this._ps.get();

    this.payments$.subscribe();
  }

  onPay(_id) {
    this._router.navigate(['payments/pay/'+_id]);
  }

  onEdit(_id) {
    this._router.navigate(['payments/pay/create/'+_id]);
  }

  private actionForSocketEvents(payment) {
    if(payment.status == 'open') {
      this._tools.getToast().toast("    ");
    } else if(payment.status == 'collected') {
      this._tools.getToast().toast("   ");
    } else {
      this._tools.getToast().toast("  !");
    }
  }

  ngOnDestroy() {
    this._ps.leaveSocket();
  }

}



