import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, FormArray, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { PersonFormComponent } from '../../persons/person-form/person-form.component';
import { PersonsService } from '../../persons/persons.service';
import { Person } from '../../persons/person'; 

import { Payment, IPayment, PaymentForm } from '../payment';

@Component({
  selector: 'app-payment-form',
  templateUrl: './payment-form.component.html',
  styleUrls: ['./payment-form.component.css']
})
export class PaymentFormComponent implements OnInit {
  @Input('formValues') payment: PaymentForm = new PaymentForm();
  @Input('services') services: any[] = [];
  //@Input('specialists') specialists: any[] = [];
  @Input('specialists') set specialists(items) {
      this._specialists = items;
      this.checkSpecialists = this.searchSpecialists(this.payment.specialists);
      this.checkAssistants = this.searchSpecialists(this.payment.assistant);
    };
  @Input('options') 
    set options(options: {
      persons: boolean;
      specialists: boolean;
      services: boolean;
      payment: boolean;
    }) {
      this._optionsView.persons = !!options.persons;
      this._optionsView.specialists = !!options.specialists;
      this._optionsView.services = !!options.services;
      this._optionsView.payment = !!options.payment;
    }
  @Input('form') form: FormGroup = this.getFormGroupFull();

  @ViewChild(PersonFormComponent) personForm: PersonFormComponent;

  @Output('submitForm') 
    eSubmit: EventEmitter<IPayment> = new EventEmitter<IPayment>();
  @Output('searchPerson')
    eSearchPerson: EventEmitter<string> = new EventEmitter<string>();
  @Output('close')
    eClose: EventEmitter<string> = new EventEmitter<string>();

  
  foundPersons: Person[];
  _optionsView = {
    persons: true,
    specialists: true,
    services: false,
    payment: true
  };
  _specialists: any[] = [];
  checkSpecialists: any[] = [];
  checkAssistants: any[] = [];

  get specialists() {
    return this._specialists;
  }
  get options() {
    return this._optionsView;
  }

  constructor(
    private fb: FormBuilder,
    private pServices: PersonsService
  ) { }

  ngOnInit() {
    this.payment.isOpen();
    this.form.patchValue(this.payment);
  }

  onSubmit() {
    let formValue = this.form.value;

    this.payment.setParams(formValue);
    //console.log(this.payment);

    this.eSubmit.emit(this.payment);
  }

  onSearch(target) {
    this.reloadPersons(this.form.controls['search'].value);
  }

  onCloseSearchPerson() {
    this.form.patchValue({
      search: null,
      personId: null
    });
    this.personForm.form.enable();
  }

  selectPerson(person: Person) {
    this.payment.setParams(this.form.value);
    this.payment.person = person;

    this.form.patchValue(this.payment);
    this.form.patchValue({personId: person._id});
    this.personForm.disable();
  }

  private getFormGroupFull() {
    return this.fb.group({
      _id: this.fb.control(''),
      personId: this.fb.control(''),
      specialists: this.fb.control(''),
      referral: this.fb.control(''),
      assistant: this.fb.control(''),
      services: this.fb.control(''),
      analyzes: this.fb.control(''),
      payment: this.fb.group({
          type: this.fb.control(''),
          paid: this.fb.control(''),
          inKass: this.fb.control('')
      }), 
      type: this.fb.control(''),
      discount: this.fb.group({
          type: this.fb.control(''),
          isPercent: this.fb.control(''),
          number: this.fb.control('')
      }),
      status: this.fb.control(''),
      search: this.fb.control(''),
      person: this.fb.group({
        first_name: this.fb.control(''),
        second_name: this.fb.control(''),
        last_name: this.fb.control(''),
        gender: this.fb.control(''),
        birthdate: this.fb.control(''),
        phone: this.fb.control(''),
        email: this.fb.control(''),
        picture: this.fb.control('')
      })
    });
  }

  private reloadPersons(search?: string) {
    let person$ = this.pServices.get({text: search});

    person$.subscribe((persons) => {
      this.foundPersons = persons.items.map((item) => {
        return new Person(item);
      });
    });
  }

  searchSpecialists(items) {
    let specialists = [];
    items.forEach((item) => {
      let found = null;
      if(found = this.getSpecialist(item)) specialists.push(found);
    });

    return specialists;
  }

  getSpecialist(item) {
      return this.specialists.find((spec) => {
        if(spec._id == item) return true;
      }) || false;
  }

  onClose() {
    this.eClose.emit();
  }

}



