import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Observable } from 'rxjs/Observable';

import { Payment, PaymentForm } from '../payment';
import { PaymentsService } from '../payments.service';
import { PaymentFormServicesSelectComponent } from '../payment-form-services-select/payment-form-services-select.component';

import { DoctorsService } from '../../+doctors/doctors.service';
import { Doctor } from '../../+doctors/doctor';

import { ToolsService } from '../../shared/tools.service';

@Component({
  selector: 'app-payment-pay',
  templateUrl: './payment-pay.component.html',
  styleUrls: ['./payment-pay.component.css']
})
export class PaymentPayComponent implements OnInit {
   // Doctors
  doctors$: Observable<Doctor[]>;

  paymentFormOptions = {
    persons: false,
    specialists: false,
    services: true,
    payment: true
  };

  form: FormGroup;
  payment: PaymentForm = new PaymentForm();

  constructor(
    private doctorsService: DoctorsService,
    private paymentService: PaymentsService,
    private fb: FormBuilder,
    private tools: ToolsService,
    private _route: ActivatedRoute,
    private _router: Router
  ) { }

  ngOnInit() {
    this.reloadDoctors();
    this.form = this.getFormGroup();

    this._route.params.forEach((item: Params) => {
      if(item['id']) {
        this.loadPayment(item['id']);
      }
    });
  }

  loadPayment(_id: string) {
    let payment$ = this.paymentService.getOne(_id);

    payment$.subscribe((item) => {
      this.payment = new PaymentForm(item);
      this.form.patchValue(this.payment);
    });
  }

  reloadDoctors() {
    this.doctors$ = this.doctorsService.get();

    this.doctors$.subscribe();
  }

  sendData(payment: PaymentForm) {
    payment.isClose();

    let paymentAdd$ = this.paymentService.update(payment);

    paymentAdd$.subscribe(
      (result) => {
        this.tools.getToast().toast('  ', 1000, 'color: green');
      },
      (errors: any[]) => {
        errors.forEach((error) => this.tools.getToast().toast(error, 3000, 'color: red'))
      });
  }

  getFormGroup() {
    return this.fb.group({
      _id: this.fb.control(''),
      personId: this.fb.control(''),
      specialists: this.fb.control(''),
      referral: this.fb.control(''),
      assistant: this.fb.control(''),
      payment: this.fb.group({
          type: this.fb.control('', Validators.required),
          paid: this.fb.control('', Validators.required),
          inKass: this.fb.control('')
      }), 
      type: this.fb.control('', Validators.required),
      discount: this.fb.group({
          type: this.fb.control(''),
          isPercent: this.fb.control(''),
          number: this.fb.control('')
      }),
      status: this.fb.control(''),
      search: this.fb.control(''),
      person: this.fb.group({
        first_name: this.fb.control(''),
        second_name: this.fb.control(''),
        last_name: this.fb.control(''),
        gender: this.fb.control(''),
        birthdate: this.fb.control(''),
        phone: this.fb.control(''),
        email: this.fb.control(''),
        picture: this.fb.control('')
      })
    });
  }

  toList() {
    this._router.navigate(['payments']);
  }

}



